import React, { FC, PropsWithChildren } from "react";
import { Box, Container, Flex, Heading, chakra } from "@chakra-ui/react";
import { GatsbyImage, ImageDataLike, getImage } from "gatsby-plugin-image";

import { ResponsiveContainer } from "../atoms";
import { ChakraMDXProvider, Footer, Navigation } from "../molecules";

export type LayoutProps = PropsWithChildren<{
  title?: string;
  featuredImage?: ImageDataLike | null;
}>;

const Image = chakra(GatsbyImage);

export const Layout: FC<LayoutProps> = ({ title, featuredImage, children }) => {
  const featuredImageData = featuredImage && getImage(featuredImage);

  return (
    <ChakraMDXProvider>
      <Flex flexDirection="column" minHeight="100vh">
        <Navigation />
        <Box as="main" paddingY={[2, 6]}>
          <ResponsiveContainer marginBottom={4}>
            <Heading as="h1">{title}</Heading>
            {featuredImageData && <Image image={featuredImageData} alt="" objectFit="cover" maxHeight="200px" my={8} />}
          </ResponsiveContainer>
          <ResponsiveContainer>{children}</ResponsiveContainer>
        </Box>
        <Footer marginTop="auto" />
      </Flex>
    </ChakraMDXProvider>
  );
};
